import { UniversitiesPageModule } from './universities.module';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { UniversitiesPage } from './universities.page';

const routes: Routes = [
  {
    path: '',
    component: UniversitiesPage
  },  
  {
    path: 'university-details/:universityID',
    loadChildren: () => import('./university-details/university-details.module').then(m => m.UniversityDetailsPageModule)
  },
  {
    path: 'university-filter',
    loadChildren: () => import('./university-filter/university-filter.module').then(m => m.UniversityFilterPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class UniversitiesPageRoutingModule {}
